import { Box, Typography } from "@mui/material"
import { FullWidthBoxStyled } from "../../layout/ScrollableStack/index.styles"
import ImageCard from "../../components/ImageCard"

import { IProductContainer } from "./ProductContainer.types"

const ProductContainer = <
	T extends { name: string; icon: string; disabled?: boolean }
>({
	products
}: IProductContainer<T>) => {
	return (
		<FullWidthBoxStyled justifyContent="flex-start" flexWrap="wrap" gap={3}>
			{products.map((product, idx) => (
				<Box
					key={idx}
					display="flex"
					flexDirection="column"
					alignItems="center"
					gap={2}
				>
					<ImageCard icon={product.icon} disabled={product.disabled} />
					<Typography color="primary.900" fontWeight="bold" variant="body1">
						{product.name}
					</Typography>
				</Box>
			))}
		</FullWidthBoxStyled>
	)
}

export default ProductContainer
