import React from "react"
import { OutlinedInput, outlinedInputClasses, OutlinedInputProps, Box, Chip, Stack, InputBase } from "@mui/material"
import { styled, Theme } from "@mui/system"
import { KeyboardArrowDown } from '@mui/icons-material';

type BoxType = {
  value: string[],
  onClick: (e: React.MouseEvent) => void
}

const styling = ({ theme: {palette, spacing} }: any) => ({
  // border: `solid 1px ${palette.grey['A400']}`
  display: 'inline-flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  height: 45,
  // minWidth: 185,
  maxWidth: 250,
  backgroundColor: palette.background.default,
  borderStyle: 'solid',
  borderWidth: 1,
  borderColor: palette.grey[300],
  borderRadius: 0,
  paddingRight: spacing(1.75)
})

const StyledBox = styled(Box)(styling)
const StyledInputBox = styled(Box)(({theme: {palette, spacing}}) => ({
  padding: spacing(1.5, 3),
}))

const StyledStack = styled(Stack)(({theme: {palette, spacing}}) => ({
  overflow: 'auto',
  '-ms-overflow-style': 'none',  /* Internet Explorer 10+ */
  'scrollbar-width': 'none',  /* Firefox */
  '&::-webkit-scrollbar': { 
    display: 'none'  /* Safari and Chrome */
  }
}))
const StyledChip = styled(Chip)(({theme: {palette}}) => ({
  height: 20,
  borderRadius: 0
}))

const S_InputBox = React.forwardRef<HTMLElement | undefined, BoxType>((props: BoxType, ref: React.ForwardedRef<HTMLElement | undefined>) => (
  <StyledBox ref={ref} {...props}>
    <StyledInputBox width='90%'>
      <StyledStack direction='row' spacing={0.2}>
        {props.value.length ? props.value.map(val => <StyledChip label={val} size='small' />) : <InputBase disabled />}
      </StyledStack>
    </StyledInputBox>
    {/* <Box width='10%'> */}
      <KeyboardArrowDown />
    {/* </Box> */}
  </StyledBox>
))

export default S_InputBox