import { styled } from "@mui/material"
import { Box } from "@mui/system"
import { ISBOX } from "./IconContainer.types"

export const SBOX = styled(Box, {
	shouldForwardProp: prop => {
		return prop !== "isActive"
	}
})<ISBOX>(({ theme: { palette }, isActive, width }) => {
	return {
		// width: "100%",
		width: width ?? "100%",
		// minWidth: 60,
		height: 58,
		padding: "15px",
		border: `2px solid`,
		borderColor: isActive ? palette.primary.main : palette.secondary["800"],
		borderRadius: "7px",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		// marginLeft: "auto"
		"& > svg": {
			maxWidth: width ?? "50%"
		}
	}
})
