import React from "react"
import { IIconContainer } from "./IconContainer.types"
import { SBOX } from "./index.styles"

const IconContainer = ({
	icon: Icon,
	isActive,
	children,
	width
}: IIconContainer) => {
	return (
		<SBOX width={width} isActive={isActive}>
			{/* <img
        style={{ maxWidth: "100%" }}
        src={icon ?? "https://picsum.photos/200"}
        alt="logo"
      /> */}
			{children}
		</SBOX>
	)
}

export default IconContainer
