import {
	Box,
	Button,
	Card,
	CardActionArea,
	CardActions,
	CardHeader,
	Container,
	Divider,
	TextField,
	Typography
} from "@mui/material"
import IconContainer from "../../components/IconContainer"
import { SBox, SCardActions, SCardContent } from "./index.styles"
import Driq from "../../svgs/Driq"
import Select from "../../components/FilterInput/FilterSelect"

const AddUser = () => {
	return (
		<Container maxWidth="md" fixed>
			<Card sx={{ maxWidth: 781 }}>
				<CardHeader
					disableTypography
					title={
						<Typography variant="body1" color="primary" fontWeight={"bold"}>
							Add New User
						</Typography>
					}
				/>
				<Divider />
				<SCardContent>
					<SBox>
						<Typography sx={{ flex: 1 }}>First Name</Typography>
						<TextField sx={{ flex: 3 }} fullWidth placeholder="John" />
					</SBox>
					<SBox>
						<Typography sx={{ flex: 1 }}>Last Name</Typography>
						<TextField sx={{ flex: 3 }} fullWidth placeholder="Doe" />
					</SBox>
					<SBox>
						<Typography sx={{ flex: 1 }}>Email Address</Typography>
						<TextField
							sx={{ flex: 3 }}
							fullWidth
							placeholder="username@email.com"
						/>
					</SBox>
					<Box display="flex">
						<Typography sx={{ flex: 1 }}>Products</Typography>
						<SBox display="flex" flexDirection="column" flex={3}>
							<Box display="flex" width="100%" gap={2}>
								<Box
									component={Typography}
									sx={{ width: "20%" }}
									color="text.mute"
								>
									Select Product
								</Box>
								<Box
									component={Typography}
									sx={{ width: "40%" }}
									color="text.mute"
								>
									Select Practice
								</Box>
								<Box
									component={Typography}
									sx={{ width: "40%" }}
									color="text.mute"
								>
									Select Role
								</Box>
							</Box>
							<Box display="flex" flexDirection="column" mt={1} gap={1}>
								{new Array(6).fill(true).map((item, idx) => (
									<Box display="flex" width="100%" gap={2}>
										<Box sx={{ width: "20%", margin: "auto" }}>
											<IconContainer isActive>
												<Driq />
											</IconContainer>
										</Box>
										<Box sx={{ width: "40%" }}>
											<Select
												onChange={() => {
													console.log("Select")
												}}
											>
												<option label="Admin" value="Admin">
													Admin
												</option>
												<option label="User" value="user">
													User
												</option>
												<option label="SuperAdmin" value="superadmin">
													SuperAdmin
												</option>
											</Select>
										</Box>
										<Box sx={{ width: "40%" }}>
											<Select
												onChange={() => {
													console.log("Select")
												}}
											>
												<option label="Admin" value="Admin">
													Admin
												</option>
												<option label="User" value="user">
													User
												</option>
												<option label="SuperAdmin" value="superadmin">
													SuperAdmin
												</option>
											</Select>
										</Box>
									</Box>
								))}
							</Box>
						</SBox>
					</Box>
				</SCardContent>
				<Divider />
				<SCardActions>
					<Button variant="outlined">Cancel</Button>
					<Button variant="contained">Save</Button>
				</SCardActions>
			</Card>
		</Container>
	)
}

export default AddUser
